@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 800;
}

* {
  box-sizing: border-box;
  font-family: Avenir, sans-serif;
}

.search-bar {
  margin-top: 20px;
}

.search-bar__input {
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  font-family: inherit;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar__input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.big {
  background: url("Search.ccd3c07b.svg") 26px no-repeat;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.big:hover {
  background: url("Search.ccd3c07b.svg") 26px no-repeat, linear-gradient(#fff, #f6f6f7);
}

.small {
  background: url("Search.ccd3c07b.svg") 13px / 11px no-repeat;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.small:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__input::placeholder {
  color: #3d4e61;
  font-family: inherit;
  font-weight: 300;
}

.small::placeholder {
  font-size: 14px;
}

.search-bar__input:focus {
  border-radius: 3px;
  outline: none;
  font-weight: 800;
}

.big:focus {
  background: url("Search.ccd3c07b.svg") 26px no-repeat, linear-gradient(#fff, #f6f6f7);
}

.small:focus {
  color: #3d4e61;
  background-color: #0000;
  background-image: url("Search.ccd3c07b.svg"), linear-gradient(#fff, #f6f6f7);
  background-position: 13px, 0 0;
  background-repeat: no-repeat, repeat;
  background-size: 11px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
}

